main{
  width: 100%;
  padding-top: 85px;
}
.main-container{
  margin: auto;
  max-width: 1140px;
  margin-top: 50px;
}
.list{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
  border-radius: 10px;
  margin-bottom: 20px;
}
.list::-webkit-scrollbar {
  display: none;
}
.flex-list{
  display: flex;
  flex-direction: row;
  /* justify-content: space-evenly; */
  flex-wrap: wrap;
}
.item{
  padding: 0 10px;
}
.items{
  width: 20%;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
}:hover{text-decoration: none !important;}
.item-text {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0px 10px;
  text-align: center;
  font-family: 'Metropolis Bold';
  font-size: xx-large;
  color: #ffffff;
  box-sizing: border-box;
}
.head{
  margin-bottom: 30px;
}
.head > p{
  font-size: xx-large;
  font-family: 'Metropolis Bold';
  color: #222222;
  margin-bottom: 4px;
}
.head > span{
  font-size: small;
  font-family: 'Metropolis Regular';
  color: #666666;
}
.item-desc{
  font-family: 'Metropolis Medium';
  font-size: medium;
  width: 100%;
  padding: 10px 12px 20px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.item-title{
  margin: 0;
  line-height: 24px;
  color: #000000;
  margin-bottom: 10px;
  overflow: hidden;
   text-overflow: ellipsis;
}
.item-price{
  margin: 0;
  line-height: 16px;
  color: #DB3022;
  margin-bottom: 6px;
}
.item-shop{
  margin: 0;
  font-size: small;
  font-family: 'Metropolis Regular';
  color: #666666;
  margin-bottom: 8px;
}

.item-tags {
  position: relative;
  padding: 0px 10px;
  float: left;
}
.item-tags > img {
  border-radius: 8px;
  width: 456px;
  height: 180px;
  object-fit: cover;
  object-position: center;
}

.category{margin-bottom: 30px; padding: 0px 10px;}
.item-category{
  min-width: 206px;
  height: 220px;
  position: relative;
  float: left;
  border-radius: 8px;
}
.item-category > img{
  position: absolute;
  height: 65%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.item-card{
  width: 100%; max-width: 208px;
  box-shadow: 0px 0px 14px rgba(173, 173, 173, 0.25);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  cursor: pointer;
  margin-bottom: 20px;
}.item-card:hover{box-shadow: 0px 0px 14px 6px rgba(173, 173, 173, 0.25);}
.imgItem{
  width: 100%;
  height: 136px;
  object-fit: cover;
  object-position: center;
  border-radius: 8px 8px 0px 0px;
}
.item-rate{
  font-family: 'Metropolis Regular';
  font-style: normal;
  font-weight: normal;
  font-size: x-small;
  display: flex;
  align-items: center;
  margin-left: -2px;
}.item-rate span{margin-left: -15px; color: #222222;}

.tags{padding: 0 10px;}
.new{margin-bottom: 30px; padding: 0 10px;}
.popular{margin-bottom: 30px; padding: 0 10px;}
.search{margin-bottom: 30px; padding: 0 10px;}

.kzPUbE{
  background-color: #00000070 !important;
  box-shadow: 0 0 1px 3px #b6b6b6f5 !important;
}
.jAkCdf{background-color: #00000015 !important;}
.jAkCdf:hover:enabled,.jAkCdf:focus:enabled{background-color: #777777a2 !important; color: #ffffff !important;}
.jAkCdf:disabled {cursor: default !important;color: #ffffff !important;}

.jAmbxV{background-color: #00000015 !important;}
.jAmbxV:hover:enabled,.jAmbxV:focus:enabled{background-color: #777777a2 !important; color: #ffffff !important;}
.jAmbxV:disabled {cursor: default !important;color: #ffffff !important;}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}

@media screen and (max-width : 1140px){
  .list{
    border-radius: 0;
  }
  .items{
    width: 25%;
  }
}
@media screen and (max-width : 900px){
  main{padding-top: 65px;}
  .main-container{margin-top: 40px;}
  .item-tags > img {
    width: 341px;
  }
  .item-category{
    min-width: 151px;
    height: 165px;
  }
  .item-category > img {
    height: 55%;
  }
  .item-category .item-text{
    font-size: x-large;
  }
  .items{width: 33.33%;}
  
  .item-title{
    line-height: 18px;
    margin-bottom: 8px;
  }
  .item-shop{
    margin-bottom: 8px;
  }
}
@media screen and (max-width : 720px){
  .item-category{
    min-width: 126px;
    height: 140px;
  }
  .items{width: 33.33%;}
  .item-card{
    max-width: 150px;
  }
  .imgItem{
    height: 116px;
  }
  .item-desc{
    font-size: small;
    padding: 8px 9px 11px;
  }
  .item-shop{
    font-size: smaller;
  }
}
@media screen and (max-width : 480px){
  .items{
    width: 50%;
  }
  .item-card{
    max-width: 140px;
  }
}
@media screen and (max-width : 310px){
  .items{
    width: 100%;
  }
}